import React from 'react'

const Navbar = ({inputSearch, setInputSearch}) => {


  const inputHandler = (e) => {
    setInputSearch(e.target.value)
  }
  return (
    <nav class="navbar navbar-expand-lg bg-primary bg-opacity-75">
  <div class="container-fluid">
    <a class="navbar-brand text-light" href="#">To Do</a>
    <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span class="navbar-toggler-icon"></span>
    </button>
    <div class="collapse navbar-collapse" id="navbarSupportedContent">
     
      <form class="d-flex bg-light rounded-2" role="search">
        
        <input class="form-control me-2 border-0 " value={inputSearch} onChange={inputHandler} type="search" placeholder="Search" aria-label="Search" >


        </input>
        <button class="btn " type="submit"><i class="bi bi-search text-dark"></i></button>
        
      </form>
    </div>
    <div>
    <a class="navbar-brand text-light" href="#"><i class="bi bi-gear"></i></a>
    <a class="navbar-brand text-light" href="#"><i class="bi bi-question-lg"></i></a>
    <a class="navbar-brand text-light" href="#"><i class="bi bi-megaphone"></i></a>
    </div>


  </div>
</nav>
  )
}

export default Navbar