import React, {useState, useEffect} from 'react'
import Card from '../Card'

import './Task.css'


const Task = ({task, setTask, input, setInput, addToDos, completeTas, inputSearch, setInputSearch }) => {
  // const [input, setInput] = useState('');

  

  const deleteToDo = (id) => {
    let taskList = task;
    taskList.splice(id, 1);
    localStorage.setItem("task", JSON.stringify(taskList))
    setTask(task);
    window.location.reload()
  }

  const showMenu = () => {
    document.getElementById('side').style.display = 'block'
    document.getElementById('hideIcon').style.visibility = 'hidden'


   }

  return (
    <div className='planned p-3'>
      <div className='d-flex'>
        <div id='hideIcon' className='' onClick={showMenu}><i class="bi bi-list hideIcon" style={{fontSize: '30px'}}></i> </div>
      
    <h3 className='p-1'><i class="bi bi-house-fill px-3"></i> Tasks  </h3>
    </div>
    <hr className='px-2'></hr>
    <div className='p-4'>
    <Card toDo={task} setToDo={setTask} input={input} setInput={setInput} addToDos={addToDos}/>
    <ul className='ps-0'>
    {
    task.filter(post => {
      if (inputSearch === '') {
        return post;
      } else if (post.tasks.toLowerCase().includes(inputSearch.toLowerCase())) {
        return post;
      }
    }).map((val, ixd) => <li class="list-group-item"> <div className='card mt-3 ' >
    <div className='card-body d-flex justify-content-between '>
    {/* <div style={{ textDecoration: !val.complete ? "line-through" : "" }} >  */}
    <div className={!val.complete ? "strike" : "" } > 

      <button className='btn' onClick={() => completeTas(ixd+1)}>{val.complete ? <i class="bi bi-circle me-2" ></i>:<i class="bi bi-check-circle"></i>} </button>
       {val.tasks}
    </div>
        <button className='btn border bg-light border-secondary ' onClick={() => deleteToDo(ixd)}><i class="bi bi-trash3"></i></button>
    
    </div>
    
  </div>
  </li>

    )
  }
  </ul>
    </div>

    </div>

  )
}

export default Task