import React, {useState, useEffect} from 'react'
import Card from '../Card'
import './Planned.css'

const Planned = ({planned, setPlanned, input, setInput, addToDos, completePlanned, inputSearch, setInputSearch}) => {
 
  const deleteToDo = (id) => {
    let plannedList = planned;
    plannedList.splice(id, 1);
    localStorage.setItem("planned", JSON.stringify(plannedList))
    setPlanned(planned)
    window.location.reload()
  }
  const showMenu = () => {
    document.getElementById('side').style.display = 'block'
    document.getElementById('hideIcon').style.visibility = 'hidden'


   }

  return (
    <div className='planned p-3'>
      <div className='d-flex'>
        <div id='hideIcon' className='' onClick={showMenu}><i class="bi bi-list hideIcon" style={{fontSize: '30px'}}></i> </div>
      
    <h3 className='p-1'><i class="bi bi-calendar3 px-3"></i> Planned  </h3>
    </div>
    <hr className='px-2'></hr>
    <div className='p-4'>
    <Card toDo={planned} setToDo={setPlanned} input={input} setInput={setInput} addToDos={addToDos}/>
    <ul className='ps-0'>
    {
    planned.filter(post => {
      if (inputSearch === '') {
        return post;
      } else if (post.tasks.toLowerCase().includes(inputSearch.toLowerCase())) {
        return post;
      }
    }).map((val, ixd) => <li class="list-group-item "> <div className='card mt-3 ' >
    <div className='card-body d-flex justify-content-between'>
    {/* <div style={{ textDecoration: !val.complete ? "line-through" : "" }} >  */}
    <div className={!val.complete ? "strike" : "" } > 

      <button className='btn' onClick={() => completePlanned(ixd+1)}>{val.complete ? <i class="bi bi-circle me-2" ></i>:<i class="bi bi-check-circle"></i>} </button>
       {val.tasks}
    </div>
       
       <button className='btn border bg-light border-secondary ' onClick={() => deleteToDo(ixd)}><i class="bi bi-trash3"></i></button>
    
    </div>
    
  </div>
  </li>

    )
  }
  </ul>

    </div>
    </div>
  )
}

export default Planned