import React, {useState, useEffect} from 'react'
import {
    BrowserRouter as Router,
    Routes,
    Route,
    Link
  } from "react-router-dom";
import Navbar from './Navbar'
import SideMenu from './SideMenu'
import './Main.css'
import Planned from './sideMenu/Planned';
import MyDay from './sideMenu/MyDay';
import Important from './sideMenu/Important';
import Assign from './sideMenu/Assign';
import Task from './sideMenu/Task';
import Calender from './sideMenu/Calender';

const Main = () => {
  const [toDo, setToDo] = useState([]);
  const [important, setImportant] = useState([]);
  const [planned, setPlanned] = useState([]);
  const [task, setTask] = useState([]);
  const [input, setInput] = useState('');
  const [inputSearch, setInputSearch] = useState('')


  useEffect(() => {
    let arr = localStorage.getItem("toDo")
    if(arr){
      let obj = JSON.parse(arr)
      setToDo(obj)
    }
  }, [])

  const addToDos =() => {
    // setToDo([...toDo, input])
  let toDoList = toDo;
    toDoList.push({id: (toDoList.length+1 ), tasks: input, complete: true});
    localStorage.setItem("toDo", JSON.stringify(toDoList))
    setInput("")
    setToDo(toDoList)
  } 

  
  const handleToggle = (id) => {
    let mapped = toDo.map(task => {
      return task.id == id ? { ...task, complete: !task.complete } : { ...task};
    });
    localStorage.setItem("toDo", JSON.stringify(mapped))

    setToDo(mapped);
  }

  const updateTodoItem = (index) => {
    const newTodoItems = [...toDo];
    const item = newTodoItems[index];
    let newItem = prompt(`Update ${item.tasks}?`, item.tasks);
    let todoObj = { tasks: newItem, complete: false };
    newTodoItems.splice(index, 1, todoObj);
    if (newItem === null || newItem === "") {
    return;
    } else {
    item.tasks = newItem;
    }
    setToDo(newTodoItems);
    };

  

  useEffect(() => {
    let arr = localStorage.getItem("important")
    if(arr){
      let obj = JSON.parse(arr)
      setImportant(obj)
    }
  }, [])

  const addImportant =() => {
    let importantList = important;
    importantList.push({id: (importantList.length+1 ), tasks: input, complete: true});
    localStorage.setItem("important", JSON.stringify(importantList))
    setInput("")
    setImportant(importantList)
  } 

  


  const handleToggleImportant = (id) => {
    let mapped = important.map(task => {
      return task.id == id ? { ...task, complete: !task.complete } : { ...task};
    });
    localStorage.setItem("important", JSON.stringify(mapped))

    setImportant(mapped);
  }

  useEffect(() => {
    let arr = localStorage.getItem("planned")
    if(arr){
      let obj = JSON.parse(arr)
      setPlanned(obj)
    }
  }, [])

  const addPlanned =() => {
    let plannedList = planned;
    plannedList.push({id: (plannedList.length+1 ), tasks: input, complete: true});
    
    localStorage.setItem("planned", JSON.stringify(plannedList))
    setInput("")
    setPlanned(planned)
  } 

  const handleTogglePlanned = (id) => {
    let mapped = planned.map(task => {
      return task.id == id ? { ...task, complete: !task.complete } : { ...task};
    });
    localStorage.setItem("planned", JSON.stringify(mapped))

    setPlanned(mapped);
  }

  useEffect(() => {
    let arr = localStorage.getItem("task")
    if(arr){
      let obj = JSON.parse(arr)
      setTask(obj)
    }
  }, [])

  // const allTask = () => {
  //   setTask([...task, ...toDo])
  // }

  const addTask =() => {
    let taskList = task;
    taskList.push({id: (taskList.length+1 ), tasks: input, complete: true});
    localStorage.setItem("task", JSON.stringify(taskList))
    setInput("")
    setTask(task)
  } 

  const handleToggleTask = (id) => {
    let mapped = task.map(task => {
      return task.id == id ? { ...task, complete: !task.complete } : { ...task};
    });
    localStorage.setItem("task", JSON.stringify(mapped))

    setTask(mapped);
  }
  


  return (
    <div className=''>
      
        <Navbar inputSearch={inputSearch} setInputSearch={setInputSearch} />
        <div className='d-flex'>
            
            <SideMenu toDo={toDo} setToDo={setToDo}  important={important} planned={planned} setPlanned={setPlanned} task={task}   />
                
            
            <Routes>
                <Route path= "/planned" element={<Planned planned={planned} setPlanned={setPlanned} input={input} setInput={setInput} addToDos={addPlanned} completePlanned={handleTogglePlanned} updateTodoItem={updateTodoItem} inputSearch={inputSearch} setInputSearch={setInputSearch}/>}> 
                
                </Route>

                <Route exact path='/' element = {< MyDay  toDo={toDo} setToDo={setToDo} input={input} setInput={setInput} addToDos={addToDos} completeTask={handleToggle} inputSearch={inputSearch} setInputSearch={setInputSearch} /> }>
                    
                </Route>
                <Route path='/important' element = {< Important important={important} setImportant={setImportant} input={input} setInput={setInput} addToDos={addImportant} completeImportant={handleToggleImportant} inputSearch={inputSearch} setInputSearch={setInputSearch} /> }>
                    
                </Route>
                <Route path='/assign' element = {< Assign /> }>
                    
                </Route>
                <Route path='/task' element = {< Task task={task} setTask={setTask} input={input} setInput={setInput} addToDos={addTask} completeTas={handleToggleTask}  inputSearch={inputSearch} setInputSearch={setInputSearch} /> }>
                    
                </Route>
                <Route path='/calender' element={<Calender />} ></Route>
                <Route path='/sidemenu' element={<SideMenu />} ></Route>
            </Routes>

        </div>
        
    </div>
  )
}

export default Main