import './calender.css'
import { useNavigate } from "react-router-dom";

import format from "date-fns/format";
import getDay from "date-fns/getDay";
import parse from "date-fns/parse";
import startOfWeek from "date-fns/startOfWeek";
import React, { useState, useEffect } from "react";
import { Calendar, dateFnsLocalizer } from "react-big-calendar";
// import "react-big-calendar/lib/css/react-big-calendar.css";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";


const locales = {
    "en-US": require("date-fns/locale/en-US"),
};
const localizer = dateFnsLocalizer({
    format,
    parse,
    startOfWeek,
    getDay,
    locales,
});

const events = [
    {
        title: "Big Meeting",
        allDay: true,
        start: new Date(2021, 6, 0),
        end: new Date(2021, 6, 0),
    },

];


const Calender = () => {

    let navigate = useNavigate();
    const routeChange = () => {
        let path = '/';
        // console.log('pranali')
        // document.getElementById('side').style.display = 'none'

        navigate(path);
    }
   


    const [newEvent, setNewEvent] = useState({ title: "", start: "", end: "" });
    const [allEvents, setAllEvents] = useState(events);

    useEffect(() => {
        let arr = localStorage.getItem("allEvents")
        if (arr) {
            let obj = JSON.parse(arr)
            setAllEvents(obj)
        }
    }, [])

    function handleAddEvent() {
        let newAllEvent = [...allEvents, newEvent]
        localStorage.setItem("allEvents", JSON.stringify(newAllEvent))
        setAllEvents(newAllEvent);
        setNewEvent("")
    }

    function onSelectEvent(events) {
        const r = window.confirm("Would you like to remove this event?")
        if(r === true){
          
        //   setAllEvents((prevState, props) => {
            const events = [...allEvents]
            const idx = allEvents.indexOf(events)
            events.splice(idx, 1);
        localStorage.setItem("allEvents", JSON.stringify(events))

            setAllEvents(events)
            // return { events };
        //   });
        }
      }



    return (
        <div className='calender'>
            <div className='calSideMenu border-end'>
                <div id='' title='Email' className=' hOver'>
                    <i class="bi bi-envelope fs-3"></i>
                </div>

                <div title='Calender' className=' hOver'>
                    <i class="bi bi-calendar3 fs-3 "></i>
                </div>
                <div title='People' className=' hOver'>
                    <i class="bi bi-people fs-3"></i>
                </div>
                <div title='Files' className=' hOver'>
                    <i class="bi bi-paperclip fs-3"></i>
                </div>
                <div title='ToDo' className=' hOver' onClick={routeChange}>
                    <i class="bi bi-check-square fs-3 "></i>
                </div>
            </div>
            <div className='cal_main'  >
                

                <div className='d-flex flex-column text-center p-3 addEvent'>
                    <h4 className='text-primary '>Add New Event</h4>
                    <div class="input-group mb-3">
                        <input type="text" placeholder="Add Event" class="form-control" value={newEvent.title} onChange={(e) => setNewEvent({ ...newEvent, title: e.target.value })} />
                    </div>
                    {/* <div className='my-2 d-flex justify-content-between'> */}
                    <div class="input-group mb-3">

                        <DatePicker placeholderText="Start Date" className="my-2 form-control" selected={newEvent.start} onChange={(start) => setNewEvent({ ...newEvent, start })} />
                    </div>

                    <div class="input-group mb-3">

                        <DatePicker placeholderText="End Date" className='form-control' selected={newEvent.end} onChange={(end) => setNewEvent({ ...newEvent, end })} />
                    </div>

                    {/* </div> */}
                    <div className='mx-auto my-3'>
                        <button className='btn btn-primary ' stlye={{ marginTop: "10px" }} onClick={handleAddEvent}>
                            Add Event
                        </button>
                    </div>
                </div>
                <Calendar localizer={localizer} events={allEvents} startAccessor="start" endAccessor="end" onSelectEvent={() => onSelectEvent(allEvents)} className='calendarHW' />
            </div>


        </div>
    )
}

export default Calender