import React, {useState, useEffect} from 'react'
import Card from '../Card'

import './MyDay.css'


const MyDay = ({toDo, setToDo, input, setInput, addToDos, completeTask, inputSearch, setInputSearch}) => {
  

  

   

  
  

  const deleteToDo = (id) => {
    
    let toDoList = toDo;
    toDoList.splice(id, 1)
    localStorage.setItem("toDo", JSON.stringify(toDoList))
    setToDo(toDo);
    window.location.reload()



  }
  const showButton = () => {
    let x= document.getElementById('showbutton1').style.display = 'block'

   }

   const showMenu = () => {
    document.getElementById('side').style.display = 'block'
    document.getElementById('hideIcon').style.visibility = 'hidden'
    // document.getElementById('showIcon').style.visibility = 'hidden'


   }

   var options = {  weekday: 'long', month: 'long', day: 'numeric' };
   var prnDt =  new Date().toLocaleString('en-us', options);


  return (
    <div className='planned p-3'>
      <div className='d-flex'>
        <div  onClick={showMenu}><i class="bi bi-list" style={{fontSize: '30px'}}></i> </div>
        {/* <div id='showIcon' onClick={showMenu} ><i class="bi bi-list " style={{fontSize: '30px'}}></i> </div> */}

    <h3 className='p-1'><i class="bi bi-sun px-3"></i> My Day  </h3>
    </div>
    <div className='ms-5 text-secondary date_size' >{prnDt} </div>
    <hr className='px-2'></hr>
    <div className='p-4'>
    <Card toDo={toDo} setToDo={setToDo} input={input} setInput={setInput} addToDos={addToDos}/>
    <ul className='ps-0'>
    {
    toDo.filter(post => {
      if (inputSearch === '') {
        return post;
      } else if (post.tasks.toLowerCase().includes(inputSearch.toLowerCase())) {
        return post;
      }
    }).map((val, ixd) => <li class="list-group-item ">
       <div className='card mt-3  ' onClick={showButton}>
    <div className='card-body d-flex justify-content-between '>
      <div className={!val.complete ? "strike" : "" } > 

      <button className='btn' onClick={() => completeTask(ixd+1)}>{val.complete ? <i class="bi bi-circle me-2" ></i>:<i class="bi bi-check-circle"></i>} </button>
      {val.tasks} </div>
      <button className='btn btn-sm border bg-light border-secondary bmargin ' onClick={() => deleteToDo(ixd)}>
        <i class="bi bi-trash3"></i>
        </button>
    
    </div>
    
    
  </div>
  </li>

    )
  }
  </ul>
    </div>
    </div>
  )
}

export default MyDay